.homeThreeColumns {
  background: $yellow;
  display: grid;
}

.homeThreeColumns--left,
.homeThreeColumns--middle,
.homeThreeColumns--right {
  justify-self: center;
  align-self: center;
  text-align: center;
  font-weight: lighter;
  .homeThreeColumns__number {
    font-size: 90px;
    color: $bronze;
    margin: 0;
    font-weight: lighter;
  }
  .homeThreeColumns__services {
    font-size: 18px;
    color: $graphite;
    margin: 0;
  }
  .homeThreeColumns__text {
    font-size: 16px;
    color: $graphite;
    //margin: 0;
  }
}

@media screen and (min-width: 900px) {
  .homeThreeColumns {
    //height: 384px;
    grid-template-columns: .5fr repeat(5, 1fr) .5fr;
    grid-template-rows: 1fr;
  }
  .homeThreeColumns--left {
    grid-column: 2/3;
  }

  .homeThreeColumns--middle {
    grid-column: 4/5;
  }

  .homeThreeColumns--right {
    grid-column: 6/7;
  }
}

@media screen and (min-width: 600px) and (max-width: 899px) {
  .homeThreeColumns {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 10px;
  }
}

@media screen and (max-width: 599px) {
  .homeThreeColumns {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}
