.homeHeader {
  width: 100%;
  font-size: 38px;
  color: $graphite;
  letter-spacing: -0.76px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: .25fr 1fr .25fr;
  & .homeHeader__picture {
    grid-area: 1/1/4/2;
    //width: 100%;
    height: 963px;
    background: url("../assets/Home-Hero-Image.jpg");
    background-size: cover;
    background-position: right top;
    background-repeat: no-repeat;
  }
  & .homeHeader__buttons {
    grid-area: 2/2/3/3;
    align-self: center;
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    & a {
      color: $graphite;
      font-size: 24px;
      font-weight: lighter;
      //background: transparent;
      border: .75px solid $graphite;
      width: 155px;
      //height: 120px;
      padding: 1em;
      text-decoration: none;
    }
  }
  & .homeHeader__text{
    grid-area: 2/2/3/3;
    justify-self: center;
    text-align: center;
    &::after {
      content: url("../assets/Decoration.svg");
      //margin-bottom: 1em;
      //display: block;
      //width: 100px;
    }
    & p {
      margin: 0;
    }
  }
}

@media screen and (max-width: 800px) {
  .homeHeader {
    grid-template-columns: 1fr;
    //grid-template-rows: 1fr;
    background: url("../assets/Home-Hero-Image.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  .homeHeader__buttons {
    //grid-area: 2/2/3/3;
    //align-self: center;
    //display: flex;
    //justify-content: space-evenly;
    //text-align: center;
    flex-direction: column;
    align-items: center;
    margin-top: 290px;
  }
  a {
    margin-top: 20px;
    //background: white;
  }
}



