.logoutContainer {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  & h1 {
    grid-column: 2/3;
    text-align: center;
    align-self: center;
    &::after {
      content: url("../assets/Decoration.svg");
    }
  }
  & a {
    grid-column: 2/3;
    width: 50%;
    color: $graphite;
    font-size: 24px;
    font-weight: lighter;
    //background: transparent;
    border: .75px solid $graphite;
    //width: 140px;
    //height: 120px;
    //padding: 1em;
    text-decoration: none;
    text-align: center;
    justify-self: center;
  }
}