.navigation {
  list-style: none;
  padding: 15px;
  display: flex;
  justify-content: flex-end;
  margin: 0;
  & a{
    color: $graphite;
    margin-right: 2em;
    text-decoration: none;
  }
}

@media screen and (max-width: 450px) {
  .navigation {
    justify-content: center;
  }
}

@media screen and (max-width: 260px) {
  .navigation {
    display: block;
  }
}