.homeHamburgerMenu {
    display: none;
  & .homeHamburgerMenu--hamburger {
    margin: 1em;
  }
}

@media screen and (max-width: 600px) {
    .homeHamburgerMenu {
      display: block;
      & ul {
        list-style: none;
        padding: 0;
        margin: 1em;
      }
    }
}

