.homeSimpleSteps {
  //height: 500px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-template-rows: 1fr .25fr 1fr .25fr 1fr;
}

.homeSimpleSteps__text {
  grid-column: 5/8;
  grid-row: 1/2;
  align-self: center;
  justify-self: center;
  text-align: center;
  margin: 1em 0;
  color: $graphite;
  font-size: 38px;
  &::after {
    content: url("../assets/Decoration.svg");
    display: block;
  }
}

.homeSimpleSteps__icons {
  grid-row: 2/5;
  grid-column: 1/12;
  display: grid;
  grid-template-columns: 1fr repeat(9, 1fr) 1fr;
  //grid-template-rows: 1fr 1fr;
  background: $light_gray;
  & .homeSimpleSteps__icons--chose {
    //grid-row: 1/2;
    //grid-column: 3/5;
    align-self: center;
    justify-self: center;
    text-align: center;
    & p {
      margin: 0;
    }
    & p:first-child::before {
      content: url("../assets/Icon-1.svg");
      display: block;
    }
    & p:first-child::after {
      content: '';
      display: block;
      width: 70px;
      height: 1px;
      background: $dark_gray;
      margin: 5px auto;
    }
  }

  & .homeSimpleSteps__icons--pack {
    //grid-row: 1/2;
    //grid-column: 7/9;
    align-self: center;
    justify-self: center;
    text-align: center;
    & p {
      margin: 0;
    }
    & p:first-child::before {
      content: url("../assets/Icon-2.svg");
      display: block;
    }
    & p:first-child::after {
      content: '';
      display: block;
      width: 70px;
      height: 1px;
      background: $dark_gray;
      margin: 5px auto;
    }
  }

  & .homeSimpleSteps__icons--decide {
    //grid-row: 2/3;
    //grid-column: 3/5;
    align-self: center;
    justify-self: center;
    text-align: center;
    & p {
      margin: 0;
    }
    & p:first-child::before {
      content: url("../assets/Icon-3.svg");
      display: block;
    }
    & p:first-child::after {
      content: '';
      display: block;
      width: 70px;
      height: 1px;
      background: $dark_gray;
      margin: 5px auto;
    }
  }

  & .homeSimpleSteps__icons--deliver {
    //grid-row: 2/3;
    //grid-column: 7/9;
    align-self: center;
    justify-self: center;
    text-align: center;
    & p {
      margin: 0;
    }
    & p:first-child::before {
      content: url("../assets/Icon-4.svg");
      display: block;
    }
    & p:first-child::after {
      content: '';
      display: block;
      width: 70px;
      height: 1px;
      background: $dark_gray;
      margin: 5px auto;
    }
  }
}

.homeSimpleSteps__button {
  grid-column: 5/8;
  grid-row: 5/6;
  align-self: center;
  justify-self: center;
  //margin: 4em;
  & a {
    color: $graphite;
    font-size: 24px;
    font-weight: lighter;
    //background: transparent;
    border: .75px solid $graphite;
    width: 155px;
    //height: 120px;
    padding: 1em;
    text-decoration: none;
  }
}

@media screen and (max-width: 450px) {
  .homeSimpleSteps__icons {
    grid-template-rows: 1fr 1fr;
  }
  .homeSimpleSteps__icons--chose {
    grid-row: 1/2;
    grid-column: 3/5;
  }
  .homeSimpleSteps__icons--pack {
    grid-row: 1/2;
    grid-column: 8/10;
  }
  .homeSimpleSteps__icons--decide {
    grid-row: 2/3;
    grid-column: 3/5;
  }
  .homeSimpleSteps__icons--deliver {
    grid-row: 2/3;
    grid-column: 8/10;
  }
}

@media screen and (min-width: 451px) {
  .homeSimpleSteps__icons {
    grid-template-rows: 1fr;
  }
  .homeSimpleSteps__icons--chose {
    grid-row: 1/2;
    grid-column: 3/4;
  }
  .homeSimpleSteps__icons--pack {
    grid-row: 1/2;
    grid-column: 5/6;
  }
  .homeSimpleSteps__icons--decide {
    grid-row: 1/2;
    grid-column: 7/8;
  }
  .homeSimpleSteps__icons--deliver {
    grid-row: 1/2;
    grid-column: 9/10;
  }
}

