.homeMenu {
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 1;
  & ul {
    display: flex;
    list-style: none;
    justify-content: flex-end;
    margin: 0;
    background: white;
    padding: 15px;
    box-shadow: 0 1px 1px 1px $light_gray;
    & li {
      margin-right: 2em;
      cursor: pointer;
    }
  }
}

@media screen and (max-width: 600px) {
  .homeMenu {
    display: none;
  }
}