.homeContact {
  min-height: 1040px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}

.homeContact::after {
  content: '';
  background: url("../assets/Background-Contact-Form.jpg");
  background-size: cover;
  position: absolute;
  opacity: .2;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}

.homeContact__form {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  margin-right: 150px;
}

.homeContact__form p {
  opacity: 1;
  grid-row: 1/2;
  grid-column: 1/3;
  text-align: center;
}

.homeContact__form .homeContact__name {
  grid-column: 1/2;
  grid-row: 3/4;
  border: none;
  //border-bottom: 1px $dark_gray solid;
  margin-right: 10px;
  display: flex;
  flex-direction: column;
}

.homeContact__form .homeContact__email {
  grid-column: 2/3;
  grid-row: 3/4;
  border: none;
  //border-bottom: 1px $dark_gray solid;
  display: flex;
  flex-direction: column;
}

.homeContact__form .homeContact__message {
  grid-column: 1/3;
  grid-row: 6/7;
  border: none;
  //border-bottom: 1px $dark_gray solid;
  display: flex;
  flex-direction: column;
}

.homeContact__form input[type='submit'] {
  grid-column: 2/3;
  grid-row: 7/8;
  background: transparent;
  border: 1px $graphite solid;
  padding: 15px;
}

.homeContact__form label {
  margin-top: 20px;
}

.homeContact__form input[type='submit'] {
  margin-top: 20px;
}

@media screen and (max-width: 800px ) {
  .homeContact {
    background-position: center;
    justify-content: center;
  }
  .homeContact__form {
    margin-right: 0;
  }
}

@media screen and (max-width: 320px) {
  .homeContact__form {
    display: flex;
    flex-direction: column;
  }
}