.homeWhoWeHelpOrganizations {
  grid-column: 2/3;
  //text-align: center;
  //display: grid;
  grid-template-rows: .125fr 1fr;
  grid-template-columns: 1fr;
  color: $graphite;
  & p {
    grid-row: 1/2;
    text-align: center;
    font-size: 22px;
    margin: 10px 0 0 0;
  }
}

.homeWhoWeHelpOrganizations__organizations {
  grid-row: 2/3;
  align-items: end;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr .5fr;
  font-size: 26px;
}

.homeWhoWeHelpOrganizations__pages {
  grid-row: 4/5;
  display: flex;
  align-self: center;
  justify-self: center;
  & div {
    padding: 5px 10px;
    font-size: 24px;
    cursor: pointer;
    margin-right: 1em;
    margin-top: 20px;
  }
}

.homeWhoWeHelpOrganizations__pages div.active {
  box-shadow: 0 1px 1px 1px $light_gray;
}