.homeWhoWeHelp {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  grid-template-rows: 1fr 1fr 4fr;
  margin-top: 40px;
}

.homeWhoWeHelp__header {
  //width: 100%;
  grid-column: 2/3;
  justify-self: center;
  align-self: center;
  text-align: center;
  font-size: 38px;
  &::after {
    content: url("../assets/Decoration.svg");
    display: block;
  }
}

.homeWhoWeHelp__nav {
  grid-column: 2/3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 24px;
  & div{
    cursor: pointer;
  }
}

@media screen and (max-width: 800px)  {
  .homeWhoWeHelp {
    grid-template-columns: 1fr;
  }
  .homeWhoWeHelp__nav {
    display: block;
    & div {
      margin-bottom: 20px;
    }
  }
}
