.registrationFormContainer {
  //height: 100vh;
  display: grid;
  grid-template-rows: 50px 1fr 50px;
  grid-template-columns: .5fr 2fr .5fr;
  color: $graphite;
  & .registrationForm {
    grid-row: 2/3;
    grid-column: 2/3;
    display: grid;
    grid-template-rows: .5fr 2fr;
    grid-template-columns: 1fr;
  }
}

.registrationForm p:first-child {
  text-align: center;
  font-size: 40px;
  & p:first-of-type::after {
    content: url("../assets/Decoration.svg");
    display: block;
  }
}

.registrationForm__form {
  display: grid;
  grid-template-columns: 140px 2fr 140px;
  grid-template-rows: 1fr 1fr 1fr .25fr;
  //width: 100%;
  & div {
    grid-column: 2/3;
    display: flex;
    flex-direction: column;
    background: $light_gray;
    padding: 20px 40px;
  }
  & input:not([type='submit']) {
    //width: 100%;
    border: none;
    border-bottom: 1px $graphite solid;
    background: inherit;
    outline: none;
    height: 60px;
  }
  & input[type='submit'] {
    grid-column: 1/2;
    grid-row: 4/5;
    align-self: end;
    color: $graphite;
    font-size: 24px;
    font-weight: lighter;
    background: transparent;
    border: .75px solid $graphite;
    //width: 155px;
    //height: 120px;
    //padding: 1em;
    text-decoration: none;
  }
  & a {
    grid-column: 3/4;
    grid-row: 4/5;
    align-self: end;
    color: $graphite;
    font-size: 24px;
    font-weight: lighter;
    //background: transparent;
    border: .75px solid $graphite;
    //width: 140px;
    //height: 120px;
    //padding: 1em;
    text-decoration: none;
    text-align: center;
  }
}

.registrationForm__email p,
.registrationForm__password p,
.registrationForm__passwordRepeat p{
  text-align: center;
  margin: 0;
}

@media screen and (max-width: 450px) {
  .registrationForm__form {
    grid-template-columns: 1fr;
    grid-template-rows: 150px 150px 150px 100px;
    /* display: flex;
     flex-direction: column;
     justify-content: end;
     align-items: center;*/
    & div {
      grid-column: 1/2;
    }
    & input[type='submit'] {
      grid-column: 1/2;
      align-self: end;

    }
    & a {
      grid-column: 1/2;
      align-self: start;
    }
  }
}

