.loginFormContainer {
  display: grid;
  grid-template-rows: 50px 1fr 50px;
  grid-template-columns: .5fr 2fr .5fr;
  color: #3C3C3C; }
  .loginFormContainer .loginForm {
    grid-row: 2/3;
    grid-column: 2/3;
    display: grid;
    grid-template-rows: .5fr 2fr;
    grid-template-columns: 1fr; }

.loginForm p:first-child {
  text-align: center;
  font-size: 40px; }
  .loginForm p:first-child p:first-of-type::after {
    content: url("../assets/Decoration.svg");
    display: block; }

.loginForm__form {
  display: grid;
  grid-template-columns: 140px 2fr 140px;
  grid-template-rows: 1fr 1fr .25fr; }
  .loginForm__form div {
    grid-column: 2/3;
    display: flex;
    flex-direction: column;
    background: #F0F1F1;
    padding: 20px 40px; }
  .loginForm__form input:not([type='submit']) {
    border: none;
    border-bottom: 1px #3C3C3C solid;
    background: inherit;
    outline: none;
    height: 60px; }
  .loginForm__form input[type='submit'] {
    grid-column: 1/2;
    grid-row: 3/4;
    align-self: end;
    color: #3C3C3C;
    font-size: 24px;
    font-weight: lighter;
    background: transparent;
    border: 0.75px solid #3C3C3C;
    text-decoration: none; }
  .loginForm__form a {
    grid-column: 3/4;
    grid-row: 3/4;
    align-self: end;
    color: #3C3C3C;
    font-size: 24px;
    font-weight: lighter;
    border: 0.75px solid #3C3C3C;
    text-decoration: none;
    text-align: center; }

.loginForm__email p,
.loginForm__password p {
  text-align: center;
  margin: 0; }

@media screen and (max-width: 450px) {
