.homeAboutUs {
  //min-height: 600px;
  display: grid;
}

.homeAboutUs__picture {
  background: url("../assets/People.jpg");
  background-size: cover;
  //min-height: 400px;
  background-position: center;
}

.homeAboutUs__text {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: .125fr 1fr .125fr;
  text-align: center;
  color: $graphite;
  & p {
    justify-self: center;
    align-self: center;
  }
  & p:first-child::after {
      content: url("../assets/Decoration.svg");
      display: block;
      //margin-bottom: 1em;
  }
  & p:first-child {
    grid-column: 2/3;
    font-size: 38px;
  }
  & p:not(:first-child) {
    grid-column: 2/3;
    font-size: 30px;
  }
}

.homeAboutUs__signature {
  grid-column: 2/3;
  //align-self: center;
  justify-self: right;
  margin-right: 35px;
  &::after {
    content: url("../assets/Signature.svg");
  }
}

@media screen and (max-width: 800px) {
  .homeAboutUs {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr .5fr;
  }
  .homeAboutUs__text {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}

@media screen and (min-width: 801px) {
  .homeAboutUs {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
  }
}